
import { IonCard, IonCardTitle, IonCardSubtitle, IonSpinner, IonCardContent, IonIcon } from "@ionic/vue";
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "DashboardCard",
  props: {
    value: {
      type: Number,
      default: -1,
    },
    label: {
      type: String,
      required: true,
    },
    icon: {
      type: String,
    },
    color: {
      type: String,
      default: 'light'
    }
  },
  components: {
    IonCard,
    IonCardContent,
    IonCardTitle,
    IonCardSubtitle,
    IonSpinner,
    IonIcon,
  },
  setup(props) {
    const isLoading = computed(() => props.value === -1);
    const canShowIcon = computed(() => !!props.icon?.length)
    const loaderColor = computed(() => props.color === 'light' ? 'primary' : 'light')
    return {
      isLoading,
      canShowIcon,
      loaderColor
    };
  },
});
